import { useQuery, gql } from '@apollo/client'
import { Alert, Row } from 'react-bootstrap'

import QueryResult from './query-result'
import CountryCard from './CountryCard'

const ALL_COUNTRIES = gql`
  query AllCountries {
    allCountries {
      id
      name
      isoCode
    }
  }
`

const CountryList = () => {
  const { loading, error, data } = useQuery(ALL_COUNTRIES)
  return (
    <QueryResult loading={loading} error={error} data={data}>
      <Alert variant='success' style={{ marginTop: '1em' }}>
        <Alert.Heading>Вітаємо, любі українці!</Alert.Heading>
        <p>
          На цьому сайті зібрані контакти сертифікованих перекладачів з української мови в країнах Європи. Сертифіковані перекладачі – це ті, хто має право робити переклади офіційних документів: паспортів, витягів з реєстрів, свідоцтв, тощо. Виберіть країну та місто, у якому ви хочете зробити переклад, щоб побачити перелік наявних там перекладачів.
        </p>
        <hr />
        <p className='mb-0'>
          Ці дані взяті з відкритих джерел, тому ми поки що не можемо гарантувати їх актуальність. Але ми працюємо над перевіркою цих даних та будемо постійно оновлювати їх.
        </p>
      </Alert>
      <Row xs={2} style={{ justifyContent: 'center' }}>
        {data?.allCountries.map((country, key) => <CountryCard country={country} key={key} />)}
      </Row>
    </QueryResult>
  )
}

export default CountryList
